import React, { Component } from 'react';
import './App.css';
import dog from './dog.jpeg';
import fb from './fb1.png';
import pin from './pin.png';
import twt from './twitter.png';

class Project extends Component {
    render() {
        return (
            <div className="project">
                <table>
                    <tbody><tr>
                        <td colSpan="3"><img src={dog} alt="" height={170} width={175} /></td>
                        <td><textarea cols="30" rows="9" style={{resize:"none"}} maxLength="350" placeholder="Description:" /></td>
                    </tr></tbody>
                </table>
                <div>
                    <img src={fb} alt="" height={50} width={50} />
                    <img src={pin} alt="" height={50} width={50} />
                    <img src={twt} alt="" height={50} width={50} />
                </div>
            </div>
        );
    }
}

export default Project;


