import React, { Component } from 'react';
import './App.css';
class New extends Component {
    render() {
        return (
            <div className="login">
                Type Of Project:<br/>
                <input type="text"/> <br/> Province: <br/>
                <input type="text"/> <br/> Area: <br/>
                <input type="text"/> <br/>
                <input type="file" accept="image/*"/>
                <button onClick={this.props.send}>Submit</button>
            </div>
        );
    }
}

export default New;



